import React, { Component } from 'react';
import Location from './Location';
import WeatherData from './WeatherData';
import CircularProgress from '@material-ui/core/CircularProgress';
import PropTypes from 'prop-types';

import './estilos.css';
import {
    
} from './../constant/weather';
import transformWeather from '../services/transformWeather';
import getUrlWeatherByCity from './../services/getUrlWeatherByCity';
import getUrlWeatherByCoord from './../services/getUrlByCoord';

class WeatherLocation extends Component {

    constructor (props) {
        super(props);

        const { city, coord } = props;

        this.state = {
          data: null,
          city,
          coord
        };
    }

    updateWeatherByCity = () => {

        const api_weather = getUrlWeatherByCity(this.state.city);

        fetch(api_weather).then(response => {

            return response.json();
        }).then(data => {

            const newData = transformWeather(data);
            this.setState({ data: newData });
        });
    }

    updateWeatherByCoord = () => {

        const api_weather = getUrlWeatherByCoord(this.state.coord);

        fetch(api_weather).then(response => {

            return response.json();
        }).then(data => {

            const newData = transformWeather(data);
            this.setState({ data: newData });
        }, error => {

            console.log(error);
        });
    }
    

    componentDidMount () {

        const { city, coord } = this.state;

        if (city) {

            this.updateWeatherByCity();
        }

        if (coord) {

            this.updateWeatherByCoord();
        }
      
    }

    render () {

        const { onWeatherLocationClick } = this.props;
        const { data } = this.state;
        let ciudad = '';

        if (data) {
            const { city } = data;
            ciudad = city;
        }

        return (
            <div className='weatherLocationContainer centrar' onClick={onWeatherLocationClick}>

                <Location city={ciudad} />
                {data ?
                    <WeatherData data={data} /> : 
                    <CircularProgress />
                }
            </div>
        );
    }
}

WeatherLocation.propTypes = {
    city: PropTypes.string,
    onWeatherLocationClick: PropTypes.func
}

export default WeatherLocation;