import React ,{ Component } from 'react';
import WeatherLocation from './WeatherLocation';
import { PropTypes } from 'prop-types';


class LocationList extends Component  {

    constructor (props) {
        super(props);

        const { onSelectedLocation } = this.props;

        this.state = {
            coord: {
                latitude: null,
                longitude: null
            },
            onSelectedLocation
        };
    }

    getLocation = () => {
    
        navigator.geolocation.getCurrentPosition((data) => {
    
            const { coords: { latitude, longitude } } = data
            this.setState({ coord: {  latitude, longitude } });
    
        }, (error) => {
    
            console.log(error);
        });
    }

    componentDidMount () {

        this.getLocation();
    }

    render () {     

        const { cities, onSelectedLocation, onSelectedLocationByCoord } = this.props;
        const { coord } = this.state;

        return (
            <div>
                {
                    coord.latitude && coord.longitude ? 
                        <WeatherLocation 
                            coord={coord}
                            onWeatherLocationClick={() => onSelectedLocationByCoord(coord)}
                        /> 
                    : null
                }
                {
                    cities.map((city, index) => 
                        <WeatherLocation 
                            key={city} 
                            city={city} 
                            onWeatherLocationClick={() => onSelectedLocation(city)}
                        />
                    )
                }
            </div>
        );
    }
}

LocationList.propTypes = {
    cities: PropTypes.array.isRequired,
    onSelectedLocation: PropTypes.func,
    onSelectedLocationByCoord: PropTypes.func
}

export default LocationList;