import React, { Component } from "react";
import "./App.css";
import LocationList from "./components/LocationList";
import Grid from "@material-ui/core/Grid";

const dataCities = require("./constant/city.json");

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      seconds: 0,
      secondsInterval: 1,
      cities: ["Madrid, es", "Los Angeles, us"],
      paramSearch: "",
      placeHolder: "Ubicación",
      autocomplete: []
    };
  }

  componentDidMount() {
    setInterval(this.tickTock, this.state.secondsInterval * 1000);
  }

  handleAddWeatherLocation = () => {
    const { paramSearch } = this.state;

    if (paramSearch) {
      const paramApplied = dataCities.filter(
        element => element.name.toUpperCase() === paramSearch.toUpperCase()
      );

      if (paramApplied.length === 0) {
        console.log("No se encuentra");
      } else {
        console.log(paramApplied);
      }

      this.setState({ paramSearch: "", autocomplete: paramApplied });
    } else {
      this.setState({ placeHolder: "Error" });
    }
  };

  tickTock = () => {
    const { seconds, secondsInterval } = this.state;
    this.setState({ seconds: seconds + secondsInterval });
  };

  handleSelectionLocation = city => {
    console.log(city);
  };

  handleSelectionLocationByCoord = coord => {
    console.log(coord);
  };

  render() {
    const { seconds, cities, autocomplete } = this.state;

    return (
      <Grid container spacing={8} className="grid centrar">
        <Grid item xs={12}>
          <input
            type="text"
            onChange={text => {
              this.setState({
                paramSearch: text.target.value,
                placeHolder: "Ubicación"
              });
            }}
            value={this.state.paramSearch}
            placeholder={this.state.placeHolder}
            className="inputSearch"
          />

          <button onClick={this.handleAddWeatherLocation} className="btnSearch">
            Buscar
          </button>
        </Grid>
        <Grid item xs={12}>
          {autocomplete
            ? autocomplete.map(item => (
                <div key={item.id} className="autocomplete">
                  {item.country}, {item.name}
                </div>
              ))
            : null}
        </Grid>
        <Grid item xs={12}>
          <LocationList
            cities={cities}
            onSelectedLocation={this.handleSelectionLocation}
            onSelectedLocationByCoord={this.handleSelectionLocationByCoord}
          />
        </Grid>
        <p>Segundos: {seconds}</p>
      </Grid>
    );
  }
}

export default App;
