import React from 'react';
import WeatherIcons from 'react-weathericons';
import PropTypes from 'prop-types';
import './estilos.css';
import { 
    CLOUD,
    SUN,
    RAIN,
    THUNDER,
    DRIZZLE,
    SNOW 
} from './../constant/weather';

const icons = {
    [SUN]: 'day-sunny',
    [CLOUD]: 'cloud',
    [SNOW]: 'snow',
    [THUNDER]: 'day-thunderstore',
    [RAIN]: 'rain',
    [DRIZZLE]: 'day-showers'
};

const iconsKeysArray = Object.keys(icons);

const getWeatherIcon = weatherState => {

    const icon = icons[weatherState];

    if (icon) {

        return (
            <WeatherIcons className='wicon' name={icon} size='2x' />
        )
    } else {
        return (
            <p>Error: weatherState not correct</p>
        ) 
    }   
};

const WeatherTemperature = (props) => {

    const { temperature, weatherState } = props; 

    return (
        <div className='weatherTemperatureContainer'>
            {
                getWeatherIcon(weatherState)
            }
            <span className='temperature'>
                {' ' + temperature}
            </span>
            <span className='temperatureType'>
                {' ℃'}
            </span>
        </div>
    );
}

WeatherTemperature.propTypes = {
    temperature: PropTypes.number.isRequired,
    weatherState: PropTypes.oneOf(iconsKeysArray)
};

export default WeatherTemperature;